const Catalog: React.FC = () => {
  const [hoveredTile, setHoveredTile] = useState<number | null>(null);
  const [showAll, setShowAll] = useState(false);

  const scrollToContact = () => {
    const contactSection = document.getElementById('contact');
    if (contactSection) {
      contactSection.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  const displayedTiles = showAll ? tiles : tiles.slice(0, 6);

  return (
    <section id="catalog" className="py-20 bg-gray-50 pt-24 md:pt-28">
      <div className="container mx-auto px-4">
        {/* Заголовок */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl lg:text-5xl font-bold text-gray-900 mb-6">
            Каталог <span className="text-gradient">плитки</span>
          </h2>
          <p className="text-xl text-gray-600 max-w-2xl mx-auto">
            Выберите идеальную плитку для вашего проекта. Каждая модель создана с учетом современных тенденций и требований к качеству.
          </p>
        </motion.div>

        {/* Сетка плитки */}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {displayedTiles.map((tile, index) => (
            <motion.div
              key={tile.id}
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              whileHover={{ y: -10, scale: 1.02 }}
              transition={{ 
                duration: 0.4, 
                delay: index * 0.05,
                whileHover: { duration: 0.2 }
              }}
              className="bg-white rounded-2xl shadow-lg overflow-hidden card-hover"
              onMouseEnter={() => setHoveredTile(tile.id)}
              onMouseLeave={() => setHoveredTile(null)}
            >
              {/* 3D-подобная модель плитки */}
              <div className="h-64 bg-gradient-to-br from-gray-100 to-gray-200 relative flex items-center justify-center overflow-hidden">
                <img 
                  src={tile.image} 
                  alt={tile.name}
                  className="w-full h-full object-cover"
                />
                
                {/* Бейдж цены */}
                <div className="absolute top-4 right-4 bg-primary-500 text-white px-3 py-1 rounded-full text-sm font-semibold">
                  {tile.price}
                </div>

                {/* Плавающие частицы при наведении */}
                {hoveredTile === tile.id && (
                  <>
                    <motion.div
                      initial={{ opacity: 0, scale: 0 }}
                      animate={{ opacity: 1, scale: 1 }}
                      exit={{ opacity: 0, scale: 0 }}
                      transition={{ duration: 0.2 }}
                      className="absolute top-8 left-8 w-2 h-2 bg-primary-400 rounded-full"
                    ></motion.div>
                    <motion.div
                      initial={{ opacity: 0, scale: 0 }}
                      animate={{ opacity: 1, scale: 1 }}
                      exit={{ opacity: 0, scale: 0 }}
                      transition={{ duration: 0.2, delay: 0.05 }}
                      className="absolute bottom-8 right-8 w-2 h-2 bg-primary-300 rounded-full"
                    ></motion.div>
                  </>
                )}
              </div>

              {/* Информация о плитке */}
              <div className="p-6">
                <h3 className="text-xl font-bold text-gray-900 mb-2">
                  {tile.name}
                </h3>
                <p className="text-gray-600 mb-4">
                  {tile.description}
                </p>
                
                <div className="space-y-2 mb-6">
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-500">Цвет:</span>
                    <span className="font-medium">{tile.color}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-500">Размер:</span>
                    <span className="font-medium">{tile.size}</span>
                  </div>
                </div>

                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  transition={{ duration: 0.2 }}
                  onClick={scrollToContact}
                  className="w-full btn-primary"
                >
                  Заказать
                </motion.button>
              </div>
            </motion.div>
          ))}
        </div>

        {/* Кнопка "Показать все" */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.3 }}
          viewport={{ once: true }}
          className="text-center mt-12"
        >
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            transition={{ duration: 0.2 }}
            onClick={() => setShowAll(!showAll)}
            className="btn-secondary text-lg px-8 py-4"
          >
            {showAll ? 'Скрыть модели' : 'Посмотреть все модели'}
          </motion.button>
        </motion.div>
      </div>
    </section>
  );
};

